
// Headings
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal; }

// List
ul {
  list-style: none; }

// Form
button,
input,
select,
textarea {
  margin: 0; }

// Box sizing
html {
  box-sizing: border-box; }

body {
  margin: 0; }

* {
  &,
  &::before,
  &::after {
    box-sizing: inherit; } }

// Media
img,
audio,
video {
  height: auto;
  max-width: 100%; }

// Iframe
iframe {
  border: 0; }

// Table
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0;
  text-align: left; }
